<template>
 <v-container id="header" grid-list-md text-xs-center>
    <v-layout row wrap>
      <v-flex xs12>
        <h1 id="title">{{title}}</h1>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12>
        <h5 id="subtitle">{{subtitle}}</h5>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12>
         <v-avatar id="avatar" :tile="tile" size="192px" color="grey lighten-4" :aspect-ratio="16/9">
          <img :src="image" alt="avatar">
        </v-avatar>
      </v-flex>
    </v-layout>  
  </v-container>
</template>

<script>
import {db} from '../firebase';

export default {
  name: 'Header',
  firebase: {
    header: {
      source: db.ref('header'),
      // Optional, allows you to handle any errors.
      cancelCallback(err) {
        console.error(err);
      }
    }
  },
  computed : {
    title () {
      return this.getFirebaseValueByName("Title");
    },
    subtitle () {
      return this.getFirebaseValueByName("Subtitle");
    },
    image () {
      return this.getFirebaseValueByName("image");
    }
  },
  methods: {
    getFirebaseValueByName(key){
      var result = "";
      this.header.forEach(element => {
        if (element[".key"] == key) {
          result = element[".value"];
        }
      });
      return result;
    }
  }
}
</script>

