<template>
 <v-container id="about" grid-list-md text-xs-center>
    <v-layout row wrap>
      <v-flex xs12>
        <h1>{{title}}</h1>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12>
        <p>{{text}}</p>
      </v-flex>
    </v-layout>
    <v-layout justify-center>
      <v-flex xs1 align-center>
        <a :href="cv" target="_blank" id="cv-icon-container">
          <v-img :src="image" id="cv-icon"/>
        </a>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {db} from '../firebase';

export default {
  name: 'About',
  data () {
    return {
      title: "",
      text: "",
      image: "",
      cv: ""
    }
  },
  mounted: function () {
    db.ref('about').on('value', snapshot => {
      this.title = snapshot.val().name
      this.text = snapshot.val().text
      this.image = snapshot.val().image
      this.cv = snapshot.val().cv
    })
  }
}
</script>

