  #about{
    max-width: 100%;
    min-width: 100%;
    padding-right: 0px;
    padding-left: 0px;
    background-color: #8c43ff;
    padding: 7%;

    color: white;
  }
  #cv-icon-container{
    margin: 0, auto;
  }
  #cv-icon{
    display: table;
  margin: 0 auto;
    width: 4vw;
  }
</style>